<template>
    <div class="window-outer add-animation">
        <div class="group title">
            <button-group :active.sync="animationType" :list="animationTypeList"></button-group>
        </div>
        <div class="group">
            <div class="label"></div>
            <slider :value.sync="data.delay"></slider>
        </div>
        <div class="group">
            <div class="label"></div>
            <slider :value.sync="data.last"></slider>
        </div>
        <div class="group">
            <div class="label"></div>
            <slider :value.sync="data.repeat"></slider>
        </div>
        <div class="option-area" v-if="animationType==0">
            <div class="group">
                <div class="label">()</div>
                <slider></slider>
            </div>
            <div class="group">
                <div class="label">()</div>
                <slider></slider>
            </div>
        </div>
        <div class="option-area" v-if="animationType==1">
            <div class="group">
                <div class="label">(X)</div>
                <slider></slider>
            </div>
            <div class="group">
                <div class="label">(X)</div>
                <slider></slider>
            </div>
            <div class="group">
                <div class="label">(Y)</div>
                <slider></slider>
            </div>
            <div class="group">
                <div class="label">(Y)</div>
                <slider></slider>
            </div>
            <div class="group">
                <div class="label">(Z)</div>
                <slider></slider>
            </div>
            <div class="group">
                <div class="label">(Z)</div>
                <slider></slider>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import ButtonGroup from '../../../modules/buttonGroup'
    import slider from '../../../modules/slider'
    export default{
        typeMap:[{
            type:'opacity',
            text:''
        },{
            type:'translate',
            text:''
        },{
            type:'scale',
            text:''
        },{
            type:'rotate',
            text:''
        },{
            type:'skew',
            text:''
        }],
        data(){
            return {
                animationType:0,
                animationTypeList:this.$options.typeMap.map(n=>n.text),
                data:{
                    type:'opacity',
                    delay:0,
                    last:0,
                    repeat:0
                }
            }
        },
        components:{
            slider,ButtonGroup
        }
    }
</script>


